.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: #000;
  padding: 0px 16px;
}

.dropdown:hover .dropbtn {
  background-color: #fff;
}

.dropdown-content {
    top: 56px;
  margin-left: 0;
  overflow: hidden;
  display: none;
  position: absolute;
  background-color: #fff;
  width: 185px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.5);
  z-index: 1;
}

.dropdown-content a {
  float: none;
  color: black;
  text-decoration: none;
  display: block;
  text-align: left;
  width: 185px;
}

.dropdown-content a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

